import request from '@/utils/request'

// 
export function loginOut () {
  return request({ url: '/user/userLoginOut' })
}

// 
export function qrGetCode () {
  let url = '/account/qrGetCode'
  return request({
    url
  })
}

// 
export function qrGetStatus (data) {
  let url = '/account/qrGetStatus'
  return request({ url, data })
}

// 
export function getAccountInfo (params) {
  let url = 'account/AccountInfo'
  return request({
    url,
    data: params
  })
}
